// Variables
$dark-mode-body-bg-color: $black;
$dark-mode-text-color: $gray-500;
$dark-mode-secondary: $gray-800;
$dark-mode-bg-color: $gray-900;
$dark-mode-border-color: $gray-700;

@media (prefers-color-scheme: dark) {
  body {
    background-color: $dark-mode-body-bg-color;
    color: $dark-mode-text-color;

    @import "alert";
    @import "breadcrumb";
    @import "footer";

    .navbar {
      background: $dark-mode-body-bg-color !important;

      .navbar-brand {
        color: $navbar-dark-brand-color;

        @include hover-focus {
          color: $navbar-dark-brand-hover-color;
        }
      }

      .navbar-nav {
        .nav-link {
          color: $navbar-dark-color;

          @include hover-focus {
            color: $navbar-dark-hover-color;
          }

          &.disabled {
            color: $navbar-dark-disabled-color;
          }
        }

        .show > .nav-link,
        .active > .nav-link,
        .nav-link.show,
        .nav-link.active {
          color: $navbar-dark-active-color;
        }
      }

      .navbar-toggler {
        color: $navbar-dark-color;
        border-color: $navbar-dark-toggler-border-color;
      }

      .navbar-text {
        color: $navbar-dark-color;
        a {
          color: $navbar-dark-active-color;

          @include hover-focus {
            color: $navbar-dark-active-color;
          }
        }
      }
    }

    .btn-secondary {
      @include button-variant($dark-mode-secondary, $dark-mode-secondary);
    }

    .card {
      border-color: $dark-mode-border-color;
      background-color: $dark-mode-bg-color;
    }

    .form-control,
    .custom-file-label,
    .custom-select,
    .custom-control-label::before {
      border-color: $dark-mode-border-color;
      background-color: $dark-mode-bg-color;
      color: $dark-mode-text-color;

      &::placeholder {
        /* Chrome, Firefox, Opera, Safari 10.1+ */
        color: $dark-mode-text-color;
        opacity: 0.5; /* Firefox */
      }

      &:-ms-input-placeholder {
        /* Internet Explorer 10-11 */
        color: $dark-mode-text-color;
      }

      &::-ms-input-placeholder {
        /* Microsoft Edge */
        color: $dark-mode-text-color;
      }
    }

    .custom-file-label::after {
      background: transparent;
      color: $dark-mode-text-color;
    }

    .custom-select {
      background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 4 5'%3e%3cpath fill='%23fff' d='M2 0L0 2h4zm0 5L0 3h4z'/%3e%3c/svg%3e");
    }

    hr {
      border-color: $gray-700;
    }

    .list-group-item:not(.active) {
      background-color: $dark-mode-body-bg-color;
      border-color: $dark-mode-border-color;
    }

    .list-group-item-action:hover:not(.active) {
      background-color: $dark-mode-bg-color;
    }

    .modal-content {
      background-color: $dark-mode-bg-color;
    }

    .nav {
      &.nav-tabs {
        border-color: $dark-mode-border-color;

        .nav-link {
          &.active,
          &:hover {
            border-color: $dark-mode-border-color;
          }

          &.active {
            color: inherit;
            background-color: $dark-mode-body-bg-color;
            border-bottom-color: $dark-mode-body-bg-color;
          }
        }
      }
    }

    pre,
    .pre {
      background: $dark-mode-bg-color;
      color: $dark-mode-text-color;
    }

    &.mediawiki table,
    .table {
      color: inherit;
    }
  }
}
